<template>
  <swiper-slide >
    <div class="ani page9-1" swiper-animate-effect="bounceInDown" swiper-animate-duration="1s" swiper-animate-delay="0.0s">
      <h3>HR</h3>
    </div>
    <div class="ani page9-2">          
      <img src="../../../static/img/09.png" alt="" class="ani img0" swiper-animate-effect="flipInY" swiper-animate-duration="1s" swiper-animate-delay="0.2s">     
      <img src="../../../static/img/09_1.png" alt="" class="ani img1" swiper-animate-effect="flipInX" swiper-animate-duration="1s" swiper-animate-delay="0.4s">
      <img src="../../../static/img/09_2.png" alt="" class="ani img2" swiper-animate-effect="flipInX" swiper-animate-duration="1s" swiper-animate-delay="0.8s">
      <img src="../../../static/img/09_3.png" alt="" class="ani img3" swiper-animate-effect="flipInX" swiper-animate-duration="1s" swiper-animate-delay="0.6s">
      <img src="../../../static/img/09_4.png" alt="" class="ani img4" swiper-animate-effect="flipInX" swiper-animate-duration="1s" swiper-animate-delay="1s">
    </div>
  </swiper-slide>
</template>

<script>
import { swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'weiperSlide9',
  components: {
    swiperSlide
  }
}
</script>

<style lang="stylus">
.page9-1
  h3
    font-size 48px
    color clan
    padding 60px 0 0 60px
    margin 0
    box-sizing border-box
.page9-2
  position relative
  .img0
    width 400px
    height auto
    display block
    margin 280px auto 0 auto
  .img1
    position absolute
    top -210px
    left 60px
    width 332px
    height auto
    display block
    z-index 100
  .img2
    position absolute
    top -110px
    left 280px
    width 311px
    height auto
    display block
    z-index 101
  .img3
    position absolute
    top 180px
    left 100px
    width 340px
    height auto
    display block
    z-index 100
  .img4
    position absolute
    top 180px
    left 350px
    width 334px
    height auto
    display block
    z-index 101

    
</style>



