<template>
  <swiper-slide class="slide5">
    <p class="slide5-p1 ani" swiper-animate-effect="bounceInDown" swiper-animate-duration="1s" swiper-animate-delay="0.0s">
      
    </p>
    <div class="slide5-list">
      <div class="list wd290px ani" swiper-animate-effect="bounceInLeft" swiper-animate-duration="1s" swiper-animate-delay="0.2s">
        <p>1.</p>
        <img class="ml15px" src="../../../static/img/05_1.png" alt="">
      </div>
      <div class="list2 wd275px ani" swiper-animate-effect="bounceInRight" swiper-animate-duration="1s" swiper-animate-delay="0.4s">
        <p>2.</p>
        <img src="../../../static/img/05_2.png" alt="">
      </div>
      <div class="list2 wd275px ani" swiper-animate-effect="bounceInLeft" swiper-animate-duration="1s" swiper-animate-delay="0.6s">
        <img class="ml15px" src="../../../static/img/05_4.png" alt="">
        <p>4.</p>
      </div>
      <div class="list wd290px ani" swiper-animate-effect="bounceInRight" swiper-animate-duration="1s" swiper-animate-delay="0.8s">
        <img class="mt15px" src="../../../static/img/05_3.png" alt="">
        <p style="margin-left:10px">3.</p>
      </div>
    </div>
  </swiper-slide>
</template>
<script>
import { swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'sweiperSlide5',
  components: {
    swiperSlide
  }
}
</script>

<style lang="stylus">
.slide5
  .slide5-p1    
    font-size 48px
    font-weight 800
    color clan
    padding 60px 0 0 60px
    margin 0
    box-sizing border-box
  .slide5-list
    display flex
    margin 140px 0 0
    flex-wrap wrap
    justify-content center
    .list
      p
        font-size 26px
        color #72716F
      img 
        width 210px
        height 185px
    .list2
      p
        font-size 26px
        color #72716F
      img 
        width 185px
        height 210px   
    .ml15px
      margin-left 50px 
    .mt15px
      margin-top 25px 
      margin-left -10px
    .wd290px
      width 290px
    .wd275px
      width 275px
    .wd272px
      width 265px
</style>



