<template>
  <swiper-slide >
    <div class="ani page7-1" swiper-animate-effect="bounceInDown" swiper-animate-duration="1s" swiper-animate-delay="0.0s">
      <h3></h3>
    </div>

    <div class="page7-2">
      <div class="box">
        <h3></h3>
        <div class="ani col6" swiper-animate-effect="rotateInUpLeft" swiper-animate-duration="1s" swiper-animate-delay="0.2s">
          <img src="../../../static/img/07_1.png" class="img0">
        </div>
        <div class="ani col6" swiper-animate-effect="rotateInUpRight" swiper-animate-duration="1s" swiper-animate-delay="0.6s">
          <img src="../../../static/img/07_2.png" class="img0">
        </div>
        <div class="clear"></div>
        <div class="ani col6" swiper-animate-effect="rotateInUpLeft" swiper-animate-duration="1s" swiper-animate-delay="1s">
          <img src="../../../static/img/07_3.png" class="img0">
        </div>
        <div class="ani col6" swiper-animate-effect="rotateInUpRight" swiper-animate-duration="1s" swiper-animate-delay="1.4s">
          <img src="../../../static/img/07_4.png" class="img0">
        </div>
        <div class="clear"></div>
      </div>
    </div>

    <div class="ani page7-3" swiper-animate-effect="bounceInUp" swiper-animate-duration="1s" swiper-animate-delay="1.5s">
      <img src="../../../static/img/07.jpg">
    </div>
  </swiper-slide>
</template>

<script>
import { swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'weiperSlide9',
  components: {
    swiperSlide
  }
}
</script>

<style lang="stylus">
.bgbai
  background-color #fff
.clear
  clear both
  overflow hidden

.page7-1
  h3
    font-size 48px
    color clan
    padding 60px 0 0 60px
    margin 0
    box-sizing border-box
.page7-2
  padding 65px 60px 0px 60px  
  box-sizing border-box
  .box
    width 100%
    border 5px solid clan
    border-radius 40px
    position relative
    padding 40px 36px 0 36px
    box-sizing border-box
  h3
    position absolute
    top -50px
    left 40px
    font-size 30px
    color clan
    background-color #ffffff
    padding 0 20px
    z-index 100
  .col6
    width 50%
    display inline
    float left 
    margin-bottom 50px 
  .img0, .img1, .img2, .img3
    width auto
    height 165px
    display block
    margin 0 auto
.page7-3
  img
    width 626px
    height auto
    display block
    margin 30px auto 0 auto
</style>



