<template>
  <swiper-slide >
    <div class="ani page8-1" swiper-animate-effect="bounceInDown" swiper-animate-duration="1s" swiper-animate-delay="0.0s">
      <h3></h3>
    </div>
    <div class="ani page8-2">          
      <img src="../../../static/img/08_1.png" class="ani img0" swiper-animate-effect="flipInY" swiper-animate-duration="1s" swiper-animate-delay="0.1s">
      <img src="../../../static/img/08_2.png" class="ani img1" swiper-animate-effect="flipInY" swiper-animate-duration="1s" swiper-animate-delay="0.4s">
    </div>
    <div class="ani page8-3" swiper-animate-effect="bounceInLeft" swiper-animate-duration="1s" swiper-animate-delay="0.6s">
      <p><span></span></p>
      <p><span></span>VIP</p>
      <p><span></span></p>
      <p><span></span></p>
    </div>
  </swiper-slide>
</template>

<script>
import { swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'weiperSlide9',
  components: {
    swiperSlide
  }
}
</script>

<style lang="stylus">
.page8-1
  h3
    font-size 48px
    color clan
    padding 60px 0 0 60px
    margin 0
    box-sizing border-box
.page8-2
  .img0
    width 186px
    height auto
    display block
    margin 250px auto 0 auto
  .img1
    width 509px
    height auto
    display block
    margin -345px auto 0 auto
    z-index 100
.page8-3
  margin-top 60px
  p
    font-size 28px
    color chui
    padding 0
    margin 30px 0 0 0
    text-indent 60px
  span
    margin-right 10px
    color #3f59a7
    font-size 38px    
</style>



