<template>
  <swiper-slide >
    <div class="ani page6-1" swiper-animate-effect="bounceInDown" swiper-animate-duration="1s" swiper-animate-delay="0.0s">
      <h3></h3>
    </div>

    <div class="ani page6-2">
      <div class="ani box">
        <h3></h3>
        <div class="ani col4" swiper-animate-effect="rotateInUpLeft" swiper-animate-duration="1s" swiper-animate-delay="0.2s">
          <img src="../../../static/img/06_1.png" class="img0">
        </div>
        <div class="ani col4" swiper-animate-effect="flipInX" swiper-animate-duration="1s" swiper-animate-delay="0.6s">
          <img src="../../../static/img/06_2.png" class="img0">
        </div>
        <div class="ani col4" swiper-animate-effect="rotateInUpRight" swiper-animate-duration="1s" swiper-animate-delay="1s">
          <img src="../../../static/img/06_3.png" class="img0">
        </div>
        <div class="clear"></div>
        <div class="ani col6" swiper-animate-effect="rotateInUpLeft" swiper-animate-duration="1s" swiper-animate-delay="1.4s">
          <img src="../../../static/img/06_4.png" class="img0">
        </div>
        <div class="ani col6" swiper-animate-effect="rotateInUpRight" swiper-animate-duration="1s" swiper-animate-delay="1.8s">
          <img src="../../../static/img/06_5.png" class="img0">
        </div>
        <div class="clear"></div>
      </div>
    </div>

    <div class="ani page6-3" swiper-animate-effect="bounceInUp" swiper-animate-duration="1s" swiper-animate-delay="1.9s">
      <img src="../../../static/img/06.jpg">
    </div>
  </swiper-slide>
</template>

<script>
import { swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'weiperSlide9',
  components: {
    swiperSlide
  }
}
</script>

<style lang="stylus">
.clear
  clear both
  overflow hidden

.page6-1
  h3
    font-size 48px
    color clan
    padding 60px 0 0 60px
    margin 0
    box-sizing border-box
.page6-2
  padding 65px 60px 0px 60px  
  box-sizing border-box
  .box
    width 100%
    border 5px solid #f7b941
    border-radius 40px
    position relative
    padding 40px 36px 0 36px
    box-sizing border-box
  h3
    position absolute
    top -50px
    left 40px
    font-size 30px
    color #f7b941
    background-color #ffffff
    padding 0 20px
    z-index 100
  .col4
    width 33.33%
    display inline
    float left 
    margin-bottom 50px
  .col6
    width 50%
    display inline
    float left 
    margin-bottom 40px 
  .img0, .img1, .img2, .img3
    width auto
    height 166px
    display block
    margin 0 auto
.page6-3
  img
    width 626px
    height auto
    display block
    margin 30px auto 0 auto
</style>



