<template>
  <swiper-slide class="slid1">
    <img src="../../../static/img/01_1.png" alt="" class="slid1-img1 ani" swiper-animate-effect="flip" swiper-animate-duration="0.8s" swiper-animate-delay="0.0s">      
    <div class="bottom-slid1">  
      <img src="../../../static/img/logo_1.png" alt="" class="slid1-img2 ani" swiper-animate-effect="bounceInRight" swiper-animate-duration="1s" swiper-animate-delay="0.4s"> 
      <img src="../../../static/img/dd.png" class="slid1-img3 ani" swiper-animate-effect="flipInX" swiper-animate-duration="1s" swiper-animate-delay="0.0s" />  
    </div>    
  </swiper-slide>
</template>

<script>
import { swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'sweiperSlide1',
  components: {
    swiperSlide
  }
}
</script>

<style lang="stylus">
.slid1
  background:url('static/img/bg_01.jpg') center center no-repeat;
  background-size: cover;
  position relative
  height 100%
  .slid1-img1
    margin 76px 0 0 95px
    height auto
    width 280px
  .bottom-slid1
    position absolute
    display flex
    align-items center
    justify-content center
    bottom 60px
    right 66px
    .slid1-img2
      display inline-block
      font-size 0
      margin-top -10px
      width 92px
      height 60px
  .slid1-img3
    width 40px
    height auto  
    opacity 0.3
    position absolute
    right 280px
    -webkit-animation shangxia 0.6s linear infinite
  @-webkit-keyframes shangxia    
    0% 
      top 30px
    50% 
      top 50px 
    100% 
      top 30px
</style>



