<template>
  <swiper-slide class="slid2">
    <p class="slid2-p1 ani" swiper-animate-effect="bounceInDown" swiper-animate-duration="1s" swiper-animate-delay="0.0s">
      
    </p>
    <div class="ani list" swiper-animate-effect="bounceInRight" swiper-animate-duration="1s" swiper-animate-delay="0.2s">
      <img class="slid2-img1" src="../../../static/img/i_003.png" alt="">
      <span></span>
    </div>
    <div class="ani list mt100px ml1320px" swiper-animate-effect="bounceInRight" swiper-animate-duration="1s" swiper-animate-delay="0.4s">
      <img class="slid2-img2" src="../../../static/img/i_001.png" alt="">
      <span></span>
    </div>
    <div class="ani list mt100px ml1320px" swiper-animate-effect="bounceInRight" swiper-animate-duration="1s" swiper-animate-delay="0.6s">
      <img class="slid2-img3" src="../../../static/img/i_005.png" alt="">
      <span></span>
    </div>
    <div class="ani list mt100px" swiper-animate-effect="bounceInRight" swiper-animate-duration="1s" swiper-animate-delay="0.8s">
      <img class="slid2-img4" src="../../../static/img/i_004.png" alt="">
      <span>HR</span>
    </div>
  </swiper-slide>
</template>

<script>
import { swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'sweiperSlide2',
  components: {
    swiperSlide
  }
}
</script>

<style lang="stylus">
.slid2
  background:url('static/img/02.png') -280px 370px no-repeat;
  background-size 70%
  .slid2-p1
    font-size 48px
    color clan
    margin-top 124px
    text-align center
    font-weight bold
  .list
    display flex
    align-items center
    margin 140px 0 0 130px
    .slid2-img1,.slid2-img2,.slid2-img3,.slid2-img4
      width auto
      height 62px
      display block
      margin-right 10px
    // .slid2-img2
    //   width 30px
    //   height 50px
    // .slid2-img3
    //   width 60px
    //   height 50px
    // .slid2-img4
    //   width 54px
    //   height 50px
    span 
      font-size 36px
      color clan
      font-weight bold
  .mt100px
    margin-top 132px
  .ml1320px
    margin-left 250px

</style>



