<template>
  <swiper-slide class="slide3">
    <div class="ani p3-1" swiper-animate-effect="bounceInDown" swiper-animate-duration="1s" swiper-animate-delay="0.0s">
      <h3></h3>         
    </div>
    <p class="ani slide3-p1" swiper-animate-effect="bounceInRight" swiper-animate-duration="1s" swiper-animate-delay="0.2s">
      3
    </p>
    <div class="list">
      <img src="../../../static/img/03_1.png" alt="" class="slid3-img1 ani" swiper-animate-effect="rotateInDownRight" swiper-animate-duration="1s" swiper-animate-delay="0.1s">      
      <img src="../../../static/img/03_2.png" alt="" class="slid3-img1 ani" swiper-animate-effect="rotateInDownRight" swiper-animate-duration="1s" swiper-animate-delay="0.5s">      
      <img src="../../../static/img/03_3.png" alt="" class="slid3-img1 ani" swiper-animate-effect="rotateInDownRight" swiper-animate-duration="1s" swiper-animate-delay="1s">      
    </div>
    <p class="ani slide3-p2" swiper-animate-effect="rollIn" swiper-animate-duration="1s" swiper-animate-delay="0.8s"></p>
    <p class="ani slide3-p2 mt30" swiper-animate-effect="rollIn" swiper-animate-duration="1s" swiper-animate-delay="1s"></p>
  </swiper-slide>
</template>

<script>
import { swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'sweiperSlide3',
  components: {
    swiperSlide
  }
}
</script>

<style lang="stylus">
.slide3
  .slide3-p1
    margin 90px 0 0 60px
    color #9C9C9B
    font-size 30px
    font-weight bold
  h3
    font-size 48px
    line-height 70px
    color #013f7d
    margin 120px 0 0 60px
  .list
    display flex
    justify-content center
    .slid3-img1
      margin 116px 20px 0 0
      width 200px
      height 200px
  .slide3-p2
    font-size 40px
    margin 156px 0 0 60px
    font-weight bold
    color clan
  .mt30
    margin-top 30px
</style>



